@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



.basket {
  position: fixed;
  right: 10%;
  top: 5%;
  z-index: 30;
}
.basket__btn {
  position: relative;
}
.basket__quantity {
  position: absolute;
  right: -5px;
  bottom: -5px;
  pointer-events: none;
}

@media (max-width: 1440px) {
  .basket {
    position: fixed;
    top: auto;
    bottom: 30px;
  }
}

.goods-item {
  position: relative;
}
.goods-item__tag {
  position: absolute;
  z-index: 10;
  right: 5%;
}

.add-item-basket {
  cursor: pointer;
  color: lightgreen;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.remove-item-basket {
  cursor: pointer;
  color: tomato;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.basket-list {
  position: fixed;
  top: 50%;
  left: 50%;

  -webkit-transform: translate(-50%, -50%);

          transform: translate(-50%, -50%);
  width: 55rem;
  max-width: 95%;
  background-color: white;
  z-index: 10;
  padding: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

  -webkit-animation: showBasket 500ms ease-in-out;

          animation: showBasket 500ms ease-in-out;

  max-height: 75%;
  overflow: auto;
}
.basket-list__close {
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}

@-webkit-keyframes showBasket {
  from {
    top: 20%;
    opacity: 0;
  }

  to {
    top: 50%;
    opacity: 1;
  }
}

@keyframes showBasket {
  from {
    top: 20%;
    opacity: 0;
  }

  to {
    top: 50%;
    opacity: 1;
  }
}

body {
  font-family: 'Roboto', 'Arial', sans-serif;
}

.content {
  min-height: calc(100vh - 70px - 64px);
}

.alert-item {
  position: fixed;
  box-sizing: content-box;
  top: 15%;
  right: 10%;
  z-index: 10;
  -webkit-animation: fade-in 250ms ease-in-out;
          animation: fade-in 250ms ease-in-out;
}

@media screen and (max-width: 768px) {
  .alert-item {
    position: fixed;
    top: 2%;
    right: 0;
    -webkit-animation: none;
            animation: none;
  }
}

@-webkit-keyframes fade-in {
  from {
    right: 0%;
    opacity: 0;
  }
  to {
    right: 10%;
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    right: 0%;
    opacity: 0;
  }
  to {
    right: 10%;
    opacity: 1;
  }
}

